import './Head.css';
import {NavLink} from "react-router-dom";

const Header = (props) => {
    return (
        <div className="header">
            <NavLink activeClassName="aact" to="/Glavnaya">
                <div className="glavnaya">Главная</div>
            </NavLink>
            <NavLink activeClassName="aact" to="/Myinfo">
                <div className="myinfo">О себе</div>
            </NavLink>
            <NavLink activeClassName="aact" to="/Project">
                <div className="myprojects">Проекты</div>
            </NavLink>
        </div>
    )
}

export default Header;